import React from "react";
import { Field } from "redux-form";

const dialogsForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <Field
        placeholder="Enter your text"
        component="textarea"
        name="newMessage"
      />

      <div>
        <button>Отправить</button>
      </div>
    </form>
  );
};

export default dialogsForm;
